* {
  font-family: 'Roboto', sans-serif;

}
#signIn-header-resp,
#registration-header-resp {
  color: #ff8080;
  text-align: center;
  margin-top: 20px;
  padding: 20px;

}
/* Form Headers */
#signIn-header,
#registration-header,
.hero-img-wrapper {
  color: #ff8080;
  text-align: center;
  margin: 20px;
  padding: 20px;
}
#form-link {
  text-decoration: none;
  color: black;
}

#helper-text {
  color: rgb(252, 255, 255);
}
.form-button {
  margin: 10px;
  background-color: #b6ffce;
}

.landing-button-wrapper {
  display: flex;
  align-content:flex-end;
  justify-content: flex-end
  
}

.signin-btn-wrapper {
  display: flex;
  justify-content: space-around;
  align-content: center;
}

.landing-btn {
  margin: 10px;
  background-color: #ff8080;
  border-radius:50px;

}

 #login-button {
  margin: 10px;
  background-color: #b6ffce;

} 

.landing-btn:hover {
  margin: 10px;
  background-color: #b6ffce;
  border-radius: 30px;

}

/* Hero Image Wrapper */
.hero-img-wrapper {
  margin: 20px;
  padding: 20px;
}

/* Login and Registration Form Inputs */
.reg-form-input,
.login-form-input {
  color: #b6ffce;
}

/* #### Registration Form input, label #### */
/* label color */
.row .input-field label {
  color: #b6ffce;
}
/* icon prefix focus color */
.input-field .prefix.active {
  color: #ff8080;
}
.input-field .prefix {
  color: #ff8080;
}
/* label focus color */
.row .input-field input[type="text"]:focus + label,
.row .input-field input[type="email"]:focus + label,
.row .input-field input[type="password"]:focus + label  {
  color: #b6ffce;
}


/* label underline focus color */
.row .input-field input[type="text"]:focus,
.row .input-field input[type="email"]:focus,
.row .input-field input[type="password"]:focus  {
  border-bottom: 1px solid #b6ffce;
  box-shadow: 0 1px 0 0 #b6ffce;
}


/* {{ #### Landing Page #### }} */

#landing-row-wrapper {
  display: flex;
}

/* Carousel */
a .carousel-item {
  height: 300px;
  width: 650px;
}

#landing-carousel {
  height: 800px;
  perspective: 950px;
  transform: translateY(-100px);
}

.carousel .carousel-item {
  width: 500px;
  display: flex;
  justify-content: center;
}

a img {
  width: 100%;
  height: 300px;
}

.carousel-header {
  color: #ff8080;
  flex-grow: 1;
}
.carousel-header-rsp {
  color: #ff8080;
  margin-top: 20px;
  text-align: center;
  font-size: 40px;
}
.carousel-container{
  margin-top: 20px;

}
/* About Section */
.card-about {
  background-color: #6c5b7b;
  color: #ff8080;
  margin:25px;
}


/* {{ #### Post Form #### }} */
  /* label color */

  .input-field label {
    color: #ff8080;
  }
  .input-field label:focus {
    color: #ff8080;
  }

/* label focus color */
#post-input input[type="text"]:focus + label, 
#post-input input[type="url"]:focus + label,
#post-input textarea[type="text"]:focus + label {
  color: #ff8080;
}

/* label underline focus color */
#post-input input[type="text"]:focus,
#post-input input[type="url"]:focus,
#post-input textarea[type="text"]:focus{
  border-bottom: 1px solid #ff8080;
  box-shadow: 0 .05px 0 0 #ff8080;
}

/* icon prefix focus color */
.btn-field:hover {
  background-color:#ff8080;
  color:#6c5b7b;
}


.btn-field, input, textarea{
  color: #6c5b7b;
}

#post-form-input{

  padding:10px 60px 0px;
}

.btn-wrapper{
  margin: 30px;
}

#url-post {
  color: #ff8080;
  text-decoration: none;
}

.card-action-icon {
  color: #6c5b7b;
  text-decoration: none;
}
.material-icons {
  text-decoration:none;
}

.card-content:after {
background-color: none;
}
.card-icon {
  margin-bottom: 10px;
}